import Vivus from 'vivus';
let nameStore = []
let animStore = {}
export default function createOnce(name,duration,start,file){
    console.log(nameStore.includes(name) + "  "  +name);
    if(!nameStore.includes(name)){
        let vi = new Vivus(name,{duration:duration,start:start,file:file});
        nameStore.push(name);
        animStore[name] = vi; 
    }else{
       // animStore[name].parentEl.innerHTML = "";
       // animStore[name] = new Vivus(name, {duration:1,start:start, file:file});
    }
}



